	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Grandma and Grandpa","date":"10 Apr 2000","slug":"/2000-04-10-grandma-and-grandpa/","tags":"Y2000"},"html":"<p>My cousin Tracy wanted to see some Grandma and Grandpa pictures.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/04/crhgg1.jpg\" height=\"236\" width=\"350\" border=\"0\" alt=\"crhgg1.jpg: \" /></p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/04/crhgg2.jpg\" height=\"317\" width=\"350\" border=\"0\" alt=\"crhgg2.jpg: \" /></p>"},"next":{"fields":{"title":"Let there be light","date":"08 Apr 2000","slug":"/2000-04-08-let-there-be-light/"}},"previous":{"fields":{"title":"Certified","date":"11 Apr 2000","slug":"/2000-04-11-certified/"}}},{"node":{"fields":{"title":"Let there be light","date":"08 Apr 2000","slug":"/2000-04-08-let-there-be-light/","tags":"Y2000"},"html":"<p>CNNSI <a href=\"http://www.cnnsi.com/soccer/world/news/2000/04/07/german_rdp_ap/index.html\">Hamburg closes in on Munich and Leverkusen</a>:  \"Hamburg won its fourth straight match Friday, this time edging Rostock 1-0\" But Munich and Leverkusen both won today.</p>\n<p>My folks are still here, midway through their two-week visit. Updates are sporadic here, and Mama just declared a vacation to avoid updates at all.</p>\n<p>Major achievement: my father and I brought power to our carport, burying about 5 m of cable. We now have lights and sockets in both the carport and the shed. My father says that all the electrical parts look different than he is used to. </p>\n<p>Dave and Luke have been talking about ISPs and the \"local loop\". My employer has had good experience with our T1 (or whatever the European equivalent is called). Our downtime is maybe 5 or 10 minutes per week. Our local loop is not from Deutsche Telekom, but from HanseNet, which like our ISP is a subsidiary of the local power utility HEW.</p>"},"next":{"fields":{"title":"Tuesday","date":"04 Apr 2000","slug":"/2000-04-04-tuesday/"}},"previous":{"fields":{"title":"Grandma and Grandpa","date":"10 Apr 2000","slug":"/2000-04-10-grandma-and-grandpa/"}}},{"node":{"fields":{"title":"Tuesday","date":"04 Apr 2000","slug":"/2000-04-04-tuesday/","tags":"Y2000"},"html":"<p>In Seattle they blow up old stadiums after they build new ones. In Hamburg, they keep playing in the old <a href=\"http://www.stevie-online.de/stadion/bilder1.html\">Volksparkstadion</a> even while it's being rebuilt. Over the summer, they twisted the field 90 degrees so that the old end zone seats moved to midfield. This week they are starting on the roof fabric... several weeks behind schedule. (Best of all, the club is paying for the new stadium. Having taxpayers pay for sports stadiums is an American idocy.)</p>\n<p><a href=\"http://scriptingnews.userland.com/backissues/2000/04/03\">Scripting News</a>:  \"Now the timezone of your Manila site is a preference.\" <i>Thank you, thank you!</i></p>"},"next":{"fields":{"title":"Baptism Party","date":"03 Apr 2000","slug":"/2000-04-03-baptism-party/"}},"previous":{"fields":{"title":"Let there be light","date":"08 Apr 2000","slug":"/2000-04-08-let-there-be-light/"}}}],"pathPrefix":"","first":false,"last":false,"index":1068,"pageCount":1101}}

